.list-box {
  display: inline-flex;
  flex-direction: column;
  gap: 10px;

  padding: 0;
  margin: 0 auto 30px;

  .list-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    margin: 0 auto 0;

    .box {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 10px;

      text-align: left;

      .name {
        min-width: 110px;
        margin: 0;

        @media screen and (min-width: 768px) {
          min-width: 230px;
        }
      }

      .number {
        min-width: 80px;
        margin: 0;

        @media screen and (min-width: 768px) {
          min-width: 180px;
        }
      }
    }

    .list-button {
      width: 100px;
      height: 35px;

      font-weight: 700;
      font-size: 14px;

      text-transform: uppercase;

      border: none;
      border-radius: 5px;

      box-shadow: 0 3px 3px 0 rgb(0 0 0 / 14%), 0 1px 7px 0 rgb(0 0 0 / 12%),
        0 3px 1px -2px rgb(0 0 0 / 20%);

      cursor: pointer;

      &:hover,
      &:focus {
        background-color: rgb(196, 242, 161);
      }

      &:active {
        transform: translateY(3px);
      }
    }
  }
}
